  // }

  group_owner = (data) => {
    let group_data = [];
    let data_value = [];
    data.map((e) => {
      if (group_data.indexOf(e.user_empid) < 0) {
        group_data.push(e.user_empid);

        data_value.push({
          key: e.user_empid,
          text: e.user_fnamet + " " + e.user_lnamet,
          value: e.user_empid,
        });
      }
    });
    // console.log(group_data);
    // console.log(data_value);
    this.setState({ options_multiple: data_value });
    // return group_data;
  };

